@import url("https://fonts.googleapis.com/css2?family=Asap:wght@400;700&family=Raleway:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Asap:wght@400;700&family=Raleway:wght@700&display=swap");
.ButtonModal {
  width: 20%;
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
}

.WideButton {
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
  width: 90%;
}

#Modal {
  background-color: #FFFFFF;
  box-shadow: #000000 0 2px 16px;
  display: none;
  position: absolute;
  top: 50%;
  grid-template-columns: 1fr;
}
#Modal #Cross {
  font-size: 20px;
  justify-self: end;
  margin-right: 10px;
  width: fit-content;
  outline: none;
  border: none;
  background-color: transparent;
  margin-top: 5px;
  font-family: "Raleway";
}
#Modal #ModalTitle {
  font-family: "Raleway";
  margin-left: 15%;
}
#Modal #ModalContent {
  font-family: "Asap";
  margin-left: 15%;
}
#Modal #CloseButton {
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
  width: 15%;
  padding: 8px 0;
  justify-self: end;
  margin-right: 4%;
  margin-bottom: 15px;
  box-shadow: #000000 0 0px 3px;
}

.Display {
  display: grid !important;
  width: 90%;
}